@media only screen and (max-width: 1249px) {
  .recommendations__row > div {
    padding-left: 10px; }
  .contacts__map {
    float: none;
    width: 100%; }
  .contacts__address {
    margin-right: 0;
    margin-bottom: 25px; }
  .logo {
    font-size: 0.9375rem; }
  .navigation ul li + li {
    margin-left: 15px; }
  .research .swiper-button-next, .research .swiper-button-prev {
    display: none; }
  .tastes .swiper-button-prev {
    left: 195px; }
  .tastes .swiper-button-next {
    right: 195px; } }

@media only screen and (max-width: 991px) {
  .h1, h1 {
    font-size: 1.75rem; }
  .functions__row > div {
    flex-basis: 50%; }
    .functions__row > div:last-child {
      margin-top: 35px; }
  .recommendations__row {
    justify-content: center; }
    .recommendations__row > div {
      width: auto; }
  .recommendations__block {
    margin-bottom: 25px; }
