    font-size: 1rem; }
    .recommendations__content p {
      line-height: 22px;
      margin-bottom: 20px; }
      .recommendations__content p:last-child {
        margin-bottom: 0; }
    .recommendations__content--top {
      margin-bottom: 25px; }
  .product {
    min-height: auto; }
    .product__btn {
      height: 70px;
      line-height: 70px;
      max-width: 320px;
      font-size: 1.5625rem; }
    .product__img {
